<template>
    <form @submit="onSubmit">
    <h2 class="label-wrapper">
      <label for="new-todo-input" class="label__lg">
        What needs to be done?
      </label>
    </h2>
      <el-input
        type="text"
        id="new-todo-input"
        name="new-todo"
        autocomplete="off"
        v-model="label"
        class="input_lg" />
      <el-button type="primary" native-type="submit" plain size="large" style=" font-size: 19px;" class="btn btn__lg" >Add</el-button>
    </form>
</template>

<script>
  export default {
    methods: {
        onSubmit() {
            if (this.label === "") {
                return;
            }
            this.$emit("todo-added", this.label);
            this.label = "";
        }
    },
    data() {
        return {
            label:"",
        }
    }
  };
</script>

<style scoped>
  .input_lg {
    padding: 0.8rem 1rem 0.7rem;
    width: 430px;
    height: 50px;
    left: 0px;
    font-size: 1.9rem;
    margin-bottom: 1rem;
  }
</style>