<template>
  <div class="stack-small" v-if="!isEditing">
    <el-checkbox
      :id="id"
      :checked="isDone"
      @change="$emit('checkbox-changed', $event)"
      size="large"
      style="height:20px ;width: 50px;"
     >
      <label :for="id" class="checkbox-label">{{label}}</label>
    </el-checkbox>
    <div class="btn-group">
      <el-button type="primary" plain size="large" class="btn" @click="toggleToItemEditForm">
        Edit <span class="visually-hidden">{{ label }}</span>
      </el-button>
      <el-popconfirm
      title="Are you sure you want to delete this?"
      width="250"
      :icon="InfoFilled"
      icon-color="#F56C6C"
      @confirm="deleteToDo">
      <template #reference>
        <el-button type="danger" plain size="large" class="btn " @click="$event.stopPropagation()">
      Delete <span class="visually-hidden">{{ label }}</span>
    </el-button>
    </template>
    </el-popconfirm>
    </div>
  </div>
  <to-do-item-edit-form
    v-else
    :id="id"
    :label="label"
    @item-edited="itemEdited"
    @edit-cancelled="editCancelled">
  </to-do-item-edit-form>
</template>



<script>
  import ToDoItemEditForm from "./ToDoItemEditForm";
  export default {
  props: {
    label: { required: true, type: String },
    done: { default: false, type: Boolean },
    id: { required: true, type: String },
  },
  data() {
    return {
      isEditing: false
    };
  },
  computed: {
    isDone() {
      return this.done;
    }
  },
  methods: {
    toggleToItemEditForm() {
      this.isEditing = true;
    },
    deleteToDo() {
      this.$emit('item-deleted');
    },
    itemEdited(newLabel) {
      this.$emit('item-edited', newLabel);
      this.isEditing = false;
    },
    editCancelled() {
      this.isEditing = false;
    }


  },
  components: {
    ToDoItemEditForm
  }
};
</script>

<style scoped>
.custom-checkbox {
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 19px;
  line-height: 1.25;
  color: #0b0c0c;
  display: block;
  margin-bottom: 5px;
}

.custom-checkbox .el-checkbox__label {
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  display: inline-block;
  margin-bottom: 0;
  padding: 8px 15px 5px;
  cursor: pointer;
  touch-action: manipulation;
}

.custom-checkbox .el-checkbox__input {
  width: 100px;
  height: 100px;
}

.custom-checkbox .el-checkbox__input .el-checkbox__inner {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}

.custom-checkbox .el-checkbox__input.is-checked .el-checkbox__inner::after {
  transform: rotate(45deg) scaleY(1);
  border: solid #fff;
  border-width: 0 0 3px 3px;
  width: 50px;
  height: 20px;
  left: 50%;
  top: 20%;
}

.custom-checkbox > input:focus {
  outline: 3px dashed #fd0;
  outline-offset: 0;
  box-shadow: inset 0 0 0 2px;
}
.custom-checkbox {
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.25;
  display: block;
  position: relative;
  min-height: 40px;
  margin-bottom: 10px;
  padding-left: 40px;
  clear: left;
}
.custom-checkbox > input[type="checkbox"] {
  -webkit-font-smoothing: antialiased;
  cursor: pointer;
  position: absolute;
  z-index: 1;
  top: -2px;
  left: -2px;
  width: 44px;
  height: 44px;
  margin: 0;
  opacity: 0;
}
.custom-checkbox > .checkbox-label {
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  display: inline-block;
  margin-bottom: 0;
  padding: 8px 15px 5px;
  cursor: pointer;
  touch-action: manipulation;
}
.custom-checkbox > label::before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border: 2px solid currentcolor;
  background: transparent;
}
.custom-checkbox > input[type="checkbox"]:focus + label::before {
  border-width: 4px;
  outline: 3px dashed #228bec;
}
.custom-checkbox > label::after {
  box-sizing: content-box;
  content: "";
  position: absolute;
  top: 11px;
  left: 9px;
  width: 18px;
  height: 7px;
  transform: rotate(-45deg);
  border: solid;
  border-width: 0 0 5px 5px;
  border-top-color: transparent;
  opacity: 0;
  background: transparent;
}
.custom-checkbox > input[type="checkbox"]:checked + label::after {
  opacity: 1;
}
@media only screen and (min-width: 40rem) {
  label,
  input,
  .custom-checkbox {
    font-size: 19px;
    font-size: 1.9rem;
    line-height: 1.31579;
  }

}
.btn.btn__danger {
  font-size: 19px; 
  font-family: 'Arial', sans-serif; 
}
.btn {
  font-size: 19px; /* 调整为您想要的字体大小 */
}

</style>
